import LogoSvg from '@/assets/svg/LogoSvg.vue';
import { defineComponent } from 'vue';
import { mapState } from 'vuex';
export default defineComponent({
    name: 'Heaeder',
    components: { LogoSvg },
    computed: {
        ...mapState({
            isVisible: (state) => {
                const store = state;
                return store.currentWeatherStoreModule.headerIsVisible;
            }
        })
    },
});
